<template>
  <div class="bidding">
    <Crumbs
      :parent-manage="'资金管理'"
      :children-manage="'专账信息'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="11">
          <FormItem label="往来单位">
            <Select
              v-model="searchData.yifangId"
              transfer
              filterable
              clearable
            >
              <Option v-for="item in yiFangList" :key="item.id" :value="parseInt(item.id)">{{ item.name }}</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <!-- 提交开始和结束 -->
        <Row style="margin-top:15px;">
          <Col span="11">
          <FormItem label="日期开始时间">
            <DatePicker
              v-model="searchData.startTime"
              style="width:100%;"
              type="date"
              :options="startTimeOptions"
              placeholder="请选择日期开始时间"
              placement="bottom-start"
              @on-change="startTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="日期结束时间">
            <DatePicker
              v-model="searchData.endTime"
              style="width:100%;"
              type="date"
              :options="endTimeOptions"
              placeholder="请选择日期结束时间"
              placement="bottom-start"
              @on-change="endTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px;text-align: right;">
          <Button type="primary" @click="searchList">确认</Button>
        </Row>
      </Form>
    </Content>

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="showSelect = true"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="handleDelete"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(16)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/specialAccount/exportSpecialAccountListExcel')">
        导出
      </Button>
      <Table
        highlight-row
        class="table"
        :columns="columns"
        :data="accountList"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!-- 弹出框 -->
      <Modal
        v-model="showSelect"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">请选择收支类别</p>
        <RadioGroup v-model="contract">
          <Radio v-for="(item,key) in contractArray" :key="key" :label="key">{{ item.name }}</Radio>
        </RadioGroup>
        <p
          slot="footer"
          style="text-align: left; padding-bottom: 30px;"
        >
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="showSelect = false"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="confirmSelect()"
          >
            确认
          </Button>
        </p>
      </Modal>

      <!-- 收入 -->
      <income-model ref="income" :jie-or-dai="jieOrDai" :ke-mu-array="keMuArray" :is-create="isCreate" :is-disabled="isDisabled" :contract-type="incomeType" @refreshList="searchList"></income-model>
      <!-- 支出 -->
      <pay-model ref="pay" :jie-or-dai="jieOrDai" :ke-mu-array="keMuArray" :is-create="isCreate" :is-disabled="isDisabled" :contract-type="payType" :contract-list="contractList" :yi-fang-list="yiFangList" @refreshList="searchList"></pay-model>
    </Content>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';
import IncomeModel from './components/Accountinfo/income';
import PayModel from './components/Accountinfo/pay';
import tablePage from '@/mixins/tablePage.js';
import audit from '@/components/audit.vue'
export default {
  name: 'Bidding',
  components: { Crumbs, IncomeModel, PayModel, audit },
  mixins: [tablePage],
  data() {
    return {
      // 收入和支出
      contractArray: {
        1: {
          modal: 'income',
          name: '收入'
        },
        2: {
          modal: 'pay',
          name: '支出'
        }
      },
      keMuArray: {
        1: '在建工程',
        2: '费用',
        3: '其他'
      },
      // 借或贷
      jieOrDai: {
        1: '借',
        2: '贷'
      },
      incomeType: {
        1: '应收',
        2: '实收'
      },
      payType: {
        1: '应付',
        2: '实付'
      },
      // 合同名称列表
      contractList: [],
      // 往来单位
      yiFangList: [],
      yiFangUnit: {},
      // 单选框表格
      showSelect: false,
      // 单选组合选中值
      contract: '',
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      searchData: {
        // 合同类别
        contractType: '',
        startTime: null,
        // 时间日期
        endTime: null,
        // 乙方单位
        yifangId: null,
        ascending: false,
        orderBy: 'id',
        pageNo: 1,
        pageSize: 10
      },
      // 表格内容 字段相对应
      accountList: [],
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      listUrl: '/specialAccount/searchSpecialAccount/list',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left',
          indexMethod: this.indexMethod
        },
        {
          title: '日期',
          key: 'date',
          render: (h, params) => {
            return h('div', this.$moment(params.row.date).format('YYYY年MM月DD日'))
          }
        },
        {
          title: '凭证号',
          key: 'pingzhen',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  const row = Object.assign({}, params.row);
                  const name = this.contractArray[row.shouzhi_type].modal
                  row.date = new Date(row.date)
                  this.$nextTick(() => {
                    this.isDisabled = true;
                    this.$refs[name].showModal(row);
                  })
                }
              }
            }, params.row.pingzhen);
          }
        },
        {
          title: '摘要',
          key: 'zhaiyao'
        },
        {
          title: '对方科目',
          key: 'duifang_kemu',
          render: (h, params) => {
            return h('div', this.keMuArray[params.row.duifang_kemu])
          }
        },
        {
          title: '借(收入)方',
          key: 'jiefang_money'
        },
        {
          title: '贷(收入)方',
          key: 'daifang_money'
        },
        {
          title: '借或贷',
          key: 'jie_or_dai',
          render: (h, params) => {
            return h('div', this.jieOrDai[params.row.jie_or_dai])
          }
        },
        {
          title: '余额',
          key: 'balance'
        },
        {
          title: '往来单位',
          key: 'wanglai_danwei',
          render: (h, params) => {
            const unit = params.row.wanglai_danwei
            return h('div', unit ? this.yiFangUnit[unit] : '')
          }
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'auditStatus',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            return this.getAuditDetail(h, params)
          }
        }
      ]
    }
  },
  created() {
    this.getYiFangInfo()
    // 获取合同名称 列表
    this.getContractList();
    this.searchList()
  },
  methods: {
    searchList() {
      const that = this;
      that.search(that.getNewSearchData(), (flag, res) => {
        if (flag) {
          const data = res.data
          that.accountList = data.specialAccountModelList
          that.total = data.totalCount
        }
      })
    },
    // 获取乙方信息列表
    getYiFangInfo() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/contract/searchYiFang', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const yiFangUnit = {}
          for (const item of data) {
            yiFangUnit[item.id] = item.name
          }
          that.yiFangList = data
          that.yiFangUnit = yiFangUnit
          sessionStorage.setItem('yiFangUnit', JSON.stringify(yiFangUnit))
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 获取合同信息列表
    getContractList() {
      const that = this
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/contract/searchContractList', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.contractList = res.data.contractList
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    confirmSelect() {
      const contract = this.contract
      // 1. 收入   2. 支出
      if (!contract) {
        this.$Message.warning('您未选中任何选项');
      }
      const name = this.contractArray[contract].modal
      this.$nextTick(() => {
        this.isDisabled = false
        this.isCreate = true
        this.contract = null
        this.$refs[name].showModal();
        this.showSelect = false;
      })
    },
    getAccountDetail(callback) {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/specialAccount/seatchSpecialAccountById/' + that.currentRow.id, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          callback(res.data)
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 更新操作
    handleModify() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      const form = Object.assign({}, that.currentRow);
      // 未审核 和 审核驳回可以更改信息
      const flag = [2, 3].indexOf(form.auditStatus) !== -1
      // 判断专帐类别
      const accountType = form.shouzhi_type
      const name = that.contractArray[accountType].modal
      form.date = new Date(form.date)
      that.$nextTick(() => {
        that.isCreate = false;
        that.isDisabled = !flag;
        that.$refs[name].showModal(form);
        this.contract = null
      })
    },
    // 删除
    handleDelete() {
      this.deleteData('/specialAccount/deleteSpecialAccount/', 'id')
    }
  }
}
</script>

<style lang="scss" scoped>
  .upload_text {
    font-weight: 800;
    margin-left: 20px;
    display: inline-block;
    width: 120px;
  }
</style>



